import React from "react";
import Drumpad from "./Drumpad";
import OpenHihat from "./OpenHihat";
import drumsetdata from "../drumsetdata";

export default function Drumset() {
  return (
    <div>
      <div className="drums">
        {drumsetdata.map((d) => (
          <Drumpad
            key={d.key}
            img={d.imgURL}
            name={d.id}
            keybind={d.letter}
            soundsrc={d.soundURL}
          />
        ))}
        <OpenHihat />
      </div>
    </div>
  );
}
